ine-height: 1.3;\n}\n\n// Header Styles\nh1 a, h2 a, h3 a, h4 a, h5 a, h6 a { font-weight: inherit; }\nh1 { font-size: $h1-fontsize; line-height: 110%; margin: ($h1-fontsize / 1.5) 0 ($h1-fontsize / 2.5) 0;}\nh2 { font-size: $h2-fontsize; line-height: 110%; margin: ($h2-fontsize / 1.5) 0 ($h2-fontsize / 2.5) 0;}\nh3 { font-size: $h3-fontsize; line-height: 110%; margin: ($h3-fontsize / 1.5) 0 ($h3-fontsize / 2.5) 0;}\nh4 { font-size: $h4-fontsize; line-height: 110%; margin: ($h4-fontsize / 1.5) 0 ($h4-fontsize / 2.5) 0;}\nh5 { font-size: $h5-fontsize; line-height: 110%; margin: ($h5-fontsize / 1.5) 0 ($h5-fontsize / 2.5) 0;}\nh6 { font-size: $h6-fontsize; line-height: 110%; margin: ($h6-fontsize / 1.5) 0 ($h6-fontsize / 2.5) 0;}\n\n// Text Styles\nem { font-style: italic; }\nstrong { font-weight: 500; }\nsmall { font-size: 75%; }\n.light { font-weight: 300; }\n.thin { font-weight: 200; }\n\n\n.flow-text{\n  $i: 0;\n  @while $i <= $intervals {\n    @media only screen and (min-width : 360 + ($i * $interval-size)) {\n      font-size: 1.2rem * (1 + (.02 * $i));\n    }\n    $i: $i + 1;\n  }\n\n  // Handle below 360px screen\n  @media only screen and (max-width: 360px) {\n    font-size: 1.2rem;\n  }\n}\n","// Scale transition\n.scale-transition {\n  &.scale-out {\n    transform: scale(0);\n    transition: transform .2s !important;\n  }\n\n  &.scale-in {\n    transform: scale(1);\n  }\n\n  transition: transform .3s cubic-bezier(0.53, 0.01, 0.36, 1.63) !important;\n}","\n\n.card-panel {\n  transition: box-shadow .25s;\n  padding: $card-padding;\n  margin: $element-top-margin 0 $element-bottom-margin 0;\n  border-radius: 2px;\n  @extend .z-depth-1;\n  background-color: $card-bg-color;\n}\n\n.card {\n  position: relative;\n  margin: $element-top-margin 0 $element-bottom-margin 0;\n  background-color: $card-bg-color;\n  transition: box-shadow .25s;\n  border-radius: 2px;\n  @extend .z-depth-1;\n\n\n  .card-title {\n    font-size: 24px;\n    font-weight: 300;\n    &.activator {\n      cursor: pointer;\n    }\n  }\n\n  // Card Sizes\n  &.small, &.medium, &.large {\n    position: relative;\n\n    .card-image {\n      max-height: 60%;\n      overflow: hidden;\n    }\n    .card-image + .card-content {\n      max-height: 40%;\n    }\n    .card-content {\n      max-height: 100%;\n      overflow: hidden;\n    }\n    .card-action {\n      position: absolute;\n      bottom: 0;\n      left: 0;\n      right: 0;\n    }\n  }\n\n  &.small {\n    height: 300px;\n  }\n\n  &.medium {\n    height: 400px;\n  }\n\n  &.large {\n    height: 500px;\n  }\n\n  // Horizontal Cards\n  &.horizontal {\n    &.small, &.medium, &.large {\n      .card-image {\n        height: 100%;\n        max-height: none;\n        overflow: visible;\n\n        img {\n          height: 100%;\n        }\n      }\n    }\n\n    display: flex;\n\n    .card-image {\n      max-width: 50%;\n      img {\n        border-radius: 2px 0 0 2px;\n        max-width: 100%;\n        width: auto;\n      }\n    }\n\n    .card-stacked {\n      display: flex;\n      flex-direction: column;\n      flex: 1;\n      position: relative;\n\n      .card-content {\n        flex-grow: 1;\n      }\n    }\n  }\n\n  // Sticky Action Section\n  &.sticky-action {\n    .card-action {\n      z-index: 2;\n    }\n\n    .card-reveal {\n      z-index: 1;\n      padding-bottom: 64px;\n    }\n  }\n\n\n\n\n  .card-image {\n    position: relative;\n\n    // Image background for content\n    img {\n      display: block;\n      border-radius: 2px 2px 0 0;\n      position: relative;\n      left: 0;\n      right: 0;\n      top: 0;\n      bottom: 0;\n      width: 100%;\n    }\n\n    .card-title {\n      color: $card-bg-color;\n      position: absolute;\n      bottom: 0;\n      left: 0;\n      max-width: 100%;\n      padding: $card-padding;\n    }\n  }\n\n  .card-content {\n    padding: $card-padding;\n    border-radius: 0 0 2px 2px;\n\n    p {\n      margin: 0;\n    }\n    .card-title {\n      display: block;\n      line-height: 32px;\n      margin-bottom: 8px;\n\n      i {\n        line-height: 32px;\n      }\n    }\n  }\n\n  .card-action {\n    &:last-child {\n      border-radius: 0 0 2px 2px;\n    }\n    background-color: inherit; // Use inherit to inherit color classes\n    border-top: 1px solid rgba(160,160,160,.2);\n    position: relative;\n    padding: 16px $card-padding;\n\n    a:not(.btn):not(.btn-large):not(.btn-floating) {\n      color: $card-link-color;\n      margin-right: $card-padding;\n      transition: color .3s ease;\n      text-transform: uppercase;\n\n      &:hover { color: $card-link-color-light; }\n    }\n  }\n\n  .card-reveal {\n    padding: $card-padding;\n    position: absolute;\n    background-color: $card-bg-color;\n    width: 100%;\n    overflow-y: auto;\n    left: 0;\n    top: 100%;\n    height: 100%;\n    z-index: 3;\n    display: none;\n\n    .card-title {\n      cursor: pointer;\n      display: block;\n    }\n  }\n}\n","#toast-container {\n  display:block;\n  position: fixed;\n  z-index: 10000;\n\n  @media #{$small-and-down} {\n    min-width: 100%;\n    bottom: 0%;\n  }\n  @media #{$medium-only} {\n    left: 5%;\n    bottom: 7%;\n    max-width: 90%;\n  }\n  @media #{$large-and-up} {\n    top: 10%;\n    right: 7%;\n    max-width: 86%;\n  }\n}\n\n.toast {\n  @extend .z-depth-1;\n  border-radius: 2px;\n  top: 35px;\n  width: auto;\n  margin-top: 10px;\n  position: relative;\n  max-width:100%;\n  height: auto;\n  min-height: $toast-height;\n  line-height: 1.5em;\n  background-color: $toast-color;\n  padding: 10px 25px;\n  font-size: 1.1rem;\n  font-weight: 300;\n  color: $toast-text-color;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  cursor: default;\n\n  .toast-action {\n    color: $toast-action-color;\n    font-weight: 500;\n    margin-right: -25px;\n    margin-left: 3rem;\n  }\n\n  &.rounded{\n    border-radius: 24px;\n  }\n\n  @media #{$small-and-down} {\n    width: 100%;\n    border-radius: 0;\n  }\n}\n",".tabs {\n  &.tabs-transp