n// Checkbox Styles\n[type=\"checkbox\"] {\n  // Text Label Style\n  + span:not(.lever) {\n    position: relative;\n    padding-left: 35px;\n    cursor: pointer;\n    display: inline-block;\n    height: 25px;\n    line-height: 25px;\n    font-size: 1rem;\n    user-select: none;\n  }\n\n  /* checkbox aspect */\n  + span:not(.lever):before,\n  &:not(.filled-in) + span:not(.lever):after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 18px;\n    height: 18px;\n    z-index: 0;\n    border: 2px solid $radio-empty-color;\n    border-radius: 1px;\n    margin-top: 3px;\n    transition: .2s;\n  }\n\n  &:not(.filled-in) + span:not(.lever):after {\n    border: 0;\n    transform: scale(0);\n  }\n\n  &:not(:checked):disabled + span:not(.lever):before {\n    border: none;\n    background-color: $input-disabled-color;\n  }\n\n  // Focused styles\n  &.tabbed:focus + span:not(.lever):after {\n    transform: scale(1);\n    border: 0;\n    border-radius: 50%;\n    box-shadow: 0 0 0 10px rgba(0,0,0,.1);\n    background-color: rgba(0,0,0,.1);\n  }\n}\n\n[type=\"checkbox\"]:checked {\n  + span:not(.lever):before {\n    top: -4px;\n    left: -5px;\n    width: 12px;\n    height: 22px;\n    border-top: 2px solid transparent;\n    border-left: 2px solid transparent;\n    border-right: $radio-border;\n    border-bottom: $radio-border;\n    transform: rotate(40deg);\n    backface-visibility: hidden;\n    transform-origin: 100% 100%;\n  }\n\n  &:disabled + span:before {\n    border-right: 2px solid $input-disabled-color;\n    border-bottom: 2px solid $input-disabled-color;\n  }\n}\n\n/* Indeterminate checkbox */\n[type=\"checkbox\"]:indeterminate {\n  + span:not(.lever):before {\n    top: -11px;\n    left: -12px;\n    width: 10px;\n    height: 22px;\n    border-top: none;\n    border-left: none;\n    border-right: $radio-border;\n    border-bottom: none;\n    transform: rotate(90deg);\n    backface-visibility: hidden;\n    transform-origin: 100% 100%;\n  }\n\n  // Disabled indeterminate\n  &:disabled + span:not(.lever):before {\n    border-right: 2px solid $input-disabled-color;\n    background-color: transparent;\n  }\n}\n\n// Filled in Style\n[type=\"checkbox\"].filled-in {\n  // General\n  + span:not(.lever):after {\n    border-radius: 2px;\n  }\n\n  + span:not(.lever):before,\n  + span:not(.lever):after {\n    content: '';\n    left: 0;\n    position: absolute;\n    /* .1s delay is for check animation */\n    transition: border .25s, background-color .25s, width .20s .1s, height .20s .1s, top .20s .1s, left .20s .1s;\n    z-index: 1;\n  }\n\n  // Unchecked style\n  &:not(:checked) + span:not(.lever):before {\n    width: 0;\n    height: 0;\n    border: 3px solid transparent;\n    left: 6px;\n    top: 10px;\n    transform: rotateZ(37deg);\n    transform-origin: 100% 100%;\n  }\n\n  &:not(:checked) + span:not(.lever):after {\n    height: 20px;\n    width: 20px;\n    background-color: transparent;\n    border: 2px solid $radio-empty-color;\n    top: 0px;\n    z-index: 0;\n  }\n\n  // Checked style\n  &:checked {\n    + span:not(.lever):before {\n      top: 0;\n      left: 1px;\n      width: 8px;\n      height: 13px;\n      border-top: 2px solid transparent;\n      border-left: 2px solid transparent;\n      border-right: 2px solid $input-background;\n      border-bottom: 2px solid $input-background;\n      transform: rotateZ(37deg);\n      transform-origin: 100% 100%;\n    }\n\n    + span:not(.lever):after {\n      top: 0;\n      width: 20px;\n      height: 20px;\n      border: 2px solid $secondary-color;\n      background-color: $secondary-color;\n      z-index: 0;\n    }\n  }\n\n  // Focused styles\n  &.tabbed:focus + span:not(.lever):after {\n    border-radius: 2px;\n    border-color: $radio-empty-color;\n    background-color: rgba(0,0,0,.1);\n  }\n\n  &.tabbed:checked:focus + span:not(.lever):after {\n    border-radius: 2px;\n    background-color: $secondary-color;\n    border-color: $secondary-color;\n  }\n\n  // Disabled style\n  &:disabled:not(:checked) + span:not(.lever):before {\n    background-color: transparent;\n    border: 2px solid transparent;\n  }\n\n  &:disabled:not(:checked) + span:not(.lever):after {\n    border-color: transparent;\n    background-color: $input-disabled-solid-color;\n  }\n\n  &:disabled:checked + span:not(.lever):before {\n    background-color: transparent;\n  }\n\n  &:disabled:checked + span:not(.lever):after {\n    background-color: $input-disabled-solid-color;\n    border-color: $input-disabled-solid-color;\n  }\n}\n","/* Switch\n   ========================================================================== */\n\n.switch,\n.switch * {\n  -webkit-tap-highlight-color: transparent;\n  user-select: none;\n}\n\n.switch label {\n  cursor: pointer;\n}\n\n.switch label input[type=checkbox] {\n  opacity: 0;\n  width: 0;\n  height: 0;\n\n  &:checked + .lever {\n    background-color: $switch-checked-lever-bg;\n\n    &:before, &:after {\n      left: 18px;\n    }\n\n    &:after {\n      background-color: $switch-bg-color;\n    }\n  }\n}\n\n.switch label .lever {\n  content: \"\";\n  display: inline-block;\n  position: relative;\n  width: 36px;\n  height: 14px;\n  background-color: $switch-unchecked-lever-bg;\n  border-radius: $switch-radius;\n  margin-right: 10px;\n  transition: background 0.3s ease;\n  vertical-align: middle;\n  margin: 0 16px;\n\n  &:before, &:after {\n    content: \"\";\n    position: absolute;\n    display: inline-block;\n    width: 20px;\n    height: 20px;\n    border-radius: 50%;\n    left: 0;\n    top: -3px;\n    transition: left 0.3s ease, background .3s ease, box-shadow 0.1s ease, transform .1s ease;\n  }\n\n  &:before {\n    background-color: transparentize($switch-bg-color, .85);\n  }\n\n  &:after {\n    background-color: $switch-unchecked-bg;\n    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);\n  }\n}\n\n// Switch active style\ninput[type=checkbox]:checke